.home-page h1 {
    color: gray;
    font-family: "Playfair Display", serif;
  }
  .home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 10rem;
  }
  .home-page {
    display: flex;
    flex-wrap: wrap;
  }
  .home-page .card-img-top {
    height: 250px !important;
  }
  .filters h4 {
    margin-top: 50px;
    color: gray !important;
    font-family: "Playfair Display", serif;
  }
  .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .home-page .card img:hover {
    transform: scale(0.9);
  }
  .home-page .card-text {
    color: rgb(90, 89, 89);
  }
  .loadmore {
    color: red;
    font-weight: bold;
    font-size: 20px !important;
    text-decoration: none;
  }
  