@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&family=Poppins:wght@700&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
/* font-family: "Poppins", sans-serif; */
/* font-family: "Playfair Display", serif; */
.navbar{
  font-family: "Poppins", sans-serif;
  box-shadow: 0 8px 6px -6px gray;
  font-size: 16px;
  text-transform: uppercase;
  line-height: 26px;
  -webkit-box-shadow:0 8px 6px -6px gray ;
  border-bottom: solid gray !important;
}
.nav-link{
  font-weight: 300 !important;
}
.active{
  border-bottom: 3px solid black;
}
.navbar-brand{
  font-weight: 700;
  font-family: 'roboto',sans-serif;
  letter-spacing: 2px;
}
.footer{
  background-color:black;
  color:white;
  padding:25px;
}
.footer a{
  text-decoration: none;
  color:white;
  padding:15px;
}
.footer a:hover{
  color:rgb(255, 248, 240);
  border-bottom: 1px solid rgb(255, 248, 240);
}
/* Page Not found */
.pnf{
  display: flex;
  justify-content: center;
  min-height: 65vh;
  align-items: center;
  flex-direction: column;
}
.pnf-title{
  font-weight: 700;
  font-size: 100px;
}
.pnf-heading{
  font-weight: normal;
}
.pnf-btn{
  color:black;
  border:2px solid green;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
/* For sign up page */
.signup{
  display:flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}
.product-link{
  text-decoration: none;
  color:black !important;

}
.button{
  border: none;
}
.category-btn {
  padding: 30px 30px;
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
}
.category-btn:hover {
  background-color: black;
  color: white;
}
.category-product .card img:hover {
  transform: scale(0.9);
}
.category-product .card-text {
  color: rgb(90, 89, 89);
}
.category-product .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 10rem;
}
.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color:gray !important;
  color: white;
  padding: 20px 0px;
}